.App {
  text-align: center;
  padding: 20px;
}

input {
  margin: 5px;
  padding: 5px;
}

button {
  margin: 5px;
  padding: 5px 10px;
}

.gantt {
  margin: 20px 0;
}

.gantt-slot {
  display: inline-block;
  padding: 5px;
  border: 1px solid #ccc;
  margin: 2px;
}

table {
  margin: 20px auto;
  border-collapse: collapse;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
}